@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.infoBox {
  flex: 1 1;
  cursor: pointer;
}

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.dark--stats {
  background-color: #303030 !important;
}

.infoBox__cases {
  color: #cc1034;
  font-weight: 600;
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}

.infoBox__title {
  color: #000 !important;
  font-size: 1.2em !important;
  font-weight: bolder !important;
}

.dark--title {
  color: #A8A8A8 !important;
}

.infoBox__total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.9rem !important;
  margin-bottom: 5px !important;
}

.dark--total {
  color: #888888 !important;
}

.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red {
  border-color: #CC1034;
}

.infoBox--green {
  border-color: #7dd71d;
}

.infoBox--orange {
  border-color: #fb4443;
}

.infoBox__cases--red {
  color: rgb(204, 16, 52) !important;
}

.infoBox__cases--green {
  color: rgb(125, 215, 29) !important;
}

.infoBox__cases--orange {
  color: rgb(258, 68, 67) !important;
}
.map {
  height: 40em;
  background-color: white;
  margin-top: 5em;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
  transition: all 0.1s linear;
}

.dark--map {
  background-color: #222222;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.1);
}

.dark--popup .leaflet-popup-content-wrapper, .leaflet-popup-tip {
  background-color: #707070 !important;
}

.map .leaflet-container {
  height: 100%;
}

.map__circle {
  opacity: 0.5;
  -webkit-animation: dot-anim linear 0.8s infinite;
          animation: dot-anim linear 0.8s infinite;
}

@-webkit-keyframes dot-anim {
  45% {
    opacity: 1;
  }
  55% {
    opacity: 1;
  }
  81% {
    opacity: 0.5;
  }
}

@keyframes dot-anim {
  45% {
    opacity: 1;
  }
  55% {
    opacity: 1;
  }
  81% {
    opacity: 0.5;
  }
}

.info-flag img {
  width: 100px;
  border-radius: 5px;
}

.info-name {
  font-size: 15px;
  font-weight: bold;
  color: #555;
}

.dark--name {
  color: #C8C8C8 !important;
}

.info-flag {
  height: 80px;
  width: 100%;
  background-size: cover;
  border-radius: 8px;
}

.info-container {
  width: 150px;
}

.info-deaths, .info-recovered, .info-cases {
  font-size: 12px;
  margin-top: 5px;
}

.dark--deaths, .dark--recovered, .dark--cases {
  color: #C0C0C0 !important;
}

